import sunuiUpimg from '@/components/sunui-upimg/sunui-upimg.vue'
export default {
	components: {
		sunuiUpimg
	},
	props: {
		title: {
			type: String,
			default: ''
		},
		style: {
			type: String,
			default: 'width: 90%;height:auto;'
		},
		title_style: {
			type: String,
			default: 'width: 100%;height:auto;'
		},
		textarea_style: {
			type: String,
			default: 'width: 100%;height:auto;'
		},
		upload_style: {
			type: String,
			default: 'width: 100%;height:auto;'
		},
		placeholder: {
			type: String,
			default: '请输入问题描述'
		},
		maxlength: {
			type: Number,
			default: 255
		},
		upload_count: {
			type: Number,
			default: 1
		},
		upload_auto: {
			type: Boolean,
			default: true
		},
		upload_id:{
			type: String,
			default: "0"
		},
		required: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			customerProfile: ''
		};
	},
	methods: {
		focusEventHandle(e) {
			this.$emit('text-focus', e.value)
		},
		blurEventHandle(e) {
			this.$emit('text-blur', e.value)
		},
		inputHandle(e) {
			console.log('inputHandle...',e)
			this.$emit('text-input', e ,this.upload_id)
		},
		uploadImage(e){
			this.$emit('change',e,this.upload_id)
		}
	}
}
